        <br>
 <strong>Objetivo General</strong>
 <p>
El objetivo general de la enseanza del ingls es que los estudiantes obtengan los conocimientos necesarios para participar en prcticas sociales del lenguaje de manera oral y escrita, con hablantes nativos y no nativos del ingls, a travs de actividades que conllevan la produccin e interpretacin de diversos textos escritos y orales de naturaleza cotidiana, acadmica y literaria.
</p>
<strong>Objetivos Especficos</strong>
<p>
    Sensibilizar a los estudiantes sobre la existencia de una lengua distinta de la materna, y se familiaricen con ella.
    Que los estudiantes obtengan los conocimientos necesarios para comprender y utilizar la lengua inglesa de manera que puedan reconocer, entender y emplear expresiones ampliamente utilizadas.
    Comprender y usar el ingls para desarrollar tareas comunicativas simples y cotidianas sobre temas conocidos o habituales relacionados con los ambientes familiar y comunitario, acadmico y de formacin, o el literario y ldico.
    Que los estudiantes consoliden su dominio del ingls en situaciones comunicativas bsicas y sean capaces de participar en actividades con el lenguaje, propias de prcticas sociales del lenguaje.
</p>
<strong>Misin</strong>
<p>Proporcionar a los alumnos de las condiciones que les permitan formarse a travs de una educacin integral de calidad, incluyente, pertinente e innovadora para el desarrollo y fortalecimiento de las competencias afectivas, sociales y cognoscitivas que favorezcan las funciones mentales de razonamiento lgico, reflexin, integracin de juicios, asi como el desarrollo de su creatividad.

Establecer las estructuras dinmicas que propicien un ambiente democrtico, que permita a los alumnos cultivar una actitud que responda a la prctica de los valores y al desenvolvimiento de sus capacidades, aprovechar conscientemente su potencial e interactuar productivamente con su entorno.</p>

<strong>Visin</strong>
<p>Ofrecer una educacin de calidad que permita a los alumnos desarrollar las competencias y adquirir los aprendizajes esperados establecidos en el Plan de Estudios para Educacin Bsica 2011.

Desarrollar Las Actividades didcticas centradas en el alumno y su aprendizaje, as como formar ciudadanos que aprecien y practiquen los valores para convivir armnicamente en sociedad.</p>
